.main {
    font-family: 'Century Gothic';
}

h1 {
    color: purple;
    padding: 3px;
    padding-left: 5px;
    padding-right: 5px;
    width: fit-content;
    border-radius: 12px;
    border-style: solid;
}

.success {
    padding: 3px;
    border-style: solid;
    border-radius: 4px;
    border-color: green;
    background: lightgreen;
    margin-bottom: 6px;
    width: fit-content;
}

.error {
    padding: 3px;
    border-style: solid;
    border-radius: 4px;
    border-color: red;
    background: lightcoral;
    margin-bottom: 6px;
    width: fit-content;
}